import { defineComponent, ref, onMounted } from 'vue';
import { InviteCode } from '@/service';
import { result } from '@/helpers/utils';
import {get } from '../../../../book-mgr-be/src/routers/invite-code';
import { message } from 'ant-design-vue';
const columns = [{
    title: '',
    dataIndex: 'code', //dataIndexdb/schema/invitecode
}, {
    title: '',
    slots: {
        customRender: 'status',
    },
}, {
    title: '',
    slots: {
        customRender: 'actions',
    },
}, ];

export default defineComponent({

    setup() {
        const count = ref(1);
        const curPage = ref(1);
        const list = ref([]);
        const total = ref(0);

        const getList = async() => {
            //getList()
            const res = await InviteCode.list(curPage.value, 20);

            result(res)
                .success(({ data: { list: l, total: t } }) => {
                    list.value = l;
                    total.value = t;
                }); //be/src/routers/invite-code/index.js
        };

        onMounted(() => {
            getList(); //
        })

        const setPage = (page) => {
            curPage.value = page;
            getList();
        };

        const add = async() => {
            //addadd
            //fe/src/service/invite-code/index.jsadd
            const res = await InviteCode.add(count.value);

            result(res)
                .success(() => {
                    message.success(`${count.value}`)
                    getList();
                })
        }

        const remove = async({ _id }) => {
            const res = await InviteCode.remove(_id);

            result(res)
                .success(({ msg }) => {
                    message.success(msg);

                    getList();
                })
        };
        return {
            count,

            list,
            total,
            columns,
            curPage,
            setPage,
            add,
            remove,
        };
    }
})